export class Problem {
    constructor(type, question, answer, input_answer_hint = "") {
        this.type = type;
        this.question = question;
        this.answer = answer;
        this.input_answer_hint = input_answer_hint;
        this.result = "❓";
        this.tries = 0;
        this.time = 0;
        this.hints = 0;
    }
}
export class ProblemSet {
    constructor(title, id, gen, problems = [], resources = [], tags = [], emoji_mark = "❓") {
        this.title = title;
        this.id = id;
        this.problem_index = 0;
        this.problems = problems;
        this.tags = tags;
        this.gen = gen;
        this.resources = resources;
        this.emoji_mark = emoji_mark;
        this.last_updated = 0;
    }
}
export function has_started(problems) {
    for (let i = 0; i < problems.length; i++) {
        if (problems[i].tries >= 1) {
            return true;
        }
    }
    return false;
}
//adds back the gen() functions that were not seralized
export function merge_back_deleted_props(without, original) {
    return without.map((val) => {
        let found = original.find((hay) => val.title === hay.title);
        if (found) {
            val.gen = found.gen;
            val.resources = found.resources;
            val.tags = found.tags;
            return val;
        }
        else {
            return val;
        }
    });
}
export function gen_amount(amount = 1, f) {
    return function g() {
        let res = [];
        for (let i = 0; i < amount; i++) {
            res.push(f());
        }
        return res;
    };
}
